.friend-item {
  display: flex;
  padding: 10px;
}
.friend-item img {
  height: 80px;
  width: 80px;
  border-radius: 50%;
}
.friend-item .right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 10px;
}
.friend-item h1 {
  padding: 5px;
  margin: 0;
  font-size: 20px;
}
.friend-item p {
  padding: 0;
  margin: 0;
  font-size: 13px;
  color: grey;
}
input {
  margin-bottom: 10px;
}
